.btn {
  text-transform: uppercase;
  font-size: 14px;
  line-height: 18px;
  font-weight: normal;
  color: @bg-color;
  background-color: @general-color;
  cursor: pointer;

  &:hover {
    background-color: #5eaa2f;
  }

  &:active {
    background-color: #5eaa2f;
    color: rgba(255, 255, 255, 0.3);
  }
}
